import { deleteDoc, fetchCoworkers, fetchTeams, removeCoworker } from "../api/api";
import { GetTime } from "../main";
export default {
  name: "myTeam",
  data: function data() {
    return {
      isCo: false,
      webTitle: '团队空间',
      userId: localStorage.getItem('userId'),
      teams: [// {
        //   id: 1,
        //   name: '小学期1'
        // },
        // {
        //   id: 2,
        //   name: '小学期2'
        // }
      ],
      folderDialog: false,
      type: '',
      dialogTeam: '',
      Info: {
        id: 1,
        builder: 'Kelly',
        coworkers: [{
          userId: 1,
          userName: 'Kelly',
          isBuilder: true,
          isCollected: true,
          userImg: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg'
        }, {
          userId: 2,
          userName: 'Kelly2',
          isBuilder: false,
          isCollected: true,
          userImg: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg'
        }]
      },
      infoDialog: false,
      isHover: false,
      NAME: ''
    };
  },
  mounted: function mounted() {
    this.init();
  },
  methods: {
    init: function init() {
      var _this = this;

      fetchTeams().then(function (res) {
        if (res.status === 200) {
          _this.teams = [];
          res.data.forEach(function (i) {
            _this.teams.push({
              id: i.id,
              name: i.name,
              create_time: GetTime(i.create_time),
              builder: i.create_user.username,
              builderId: i.create_user.id
            });
          });
        }
      });
    },
    toOneTeam: function toOneTeam(id) {
      this.$router.push({
        name: 'team',
        params: {
          teamId: id
        }
      });
    },
    changeVisible: function changeVisible(val) {
      this.folderDialog = val;
      this.init();
    },
    newTeam: function newTeam() {
      this.type = 'newteam';
      this.folderDialog = true;
    },
    addPeople: function addPeople(id) {
      var _this2 = this;

      this.type = 'teamwork';
      this.dialogTeam = id;
      this.teams.forEach(function (i) {
        if (i.id + '' === id + '') {
          _this2.NAME = i.name;
          return;
        }
      });
      console.log(this.NAME);
      this.folderDialog = true;
    },
    handleClick: function handleClick(id) {
      var _this3 = this;

      this.teams.forEach(function (i) {
        if (i.id === id) {
          _this3.Info = i;
        }
      });
      fetchCoworkers(this.Info.id).then(function (res) {
        _this3.Info.coworkers = [];
        console.log(res.data);
        _this3.isCo = false;
        res.data.forEach(function (i, index) {
          if (i.id + '' === _this3.userId) {
            _this3.isCo = true;
          }

          console.log(_this3.isCo);

          if (index === 0) {
            _this3.Info.builder = i.username;
            _this3.Info.builderId = i.id;
          }

          _this3.Info.coworkers.push({
            userId: i.id,
            userName: i.username,
            userImg: i.head,
            isBuilder: i.role === 1 ? true : false
          });

          console.log(i.id, _this3.Info.coworkers);
        });
        _this3.infoDialog = true;
      }).catch(function (e) {
        _this3.$message({
          message: e.response.data,
          typd: 'error'
        });
      });
    },
    Delete: function Delete(id) {
      var _this4 = this;

      if (this.userId === this.Info.builderId) {
        var message = '确定要退出团队吗？';
        this.$confirm(message).then(function () {
          removeCoworker(id, localStorage.getItem('userId')).then(function (res) {
            if (res.status === 200) {
              _this4.$message({
                message: '退出团队成功',
                type: 'warning'
              });
            } else if (res.status == 204) {
              _this4.$message({
                message: '发生其他错误，退出团队失败',
                type: 'error'
              });
            }
          }).catch(function (e) {
            if (e.response && e.response.status === 401) {
              _this4.$message({
                message: '您没有删除权限或此人不在协作者中',
                type: 'error'
              });
            }
          });
        });
      } else {
        var _message = '确定要删除团队吗？';
        this.$confirm(_message).then(function () {
          deleteDoc(id).then(function (res) {
            if (res.status === 204) {
              _this4.$message({
                message: '删除团队成功',
                type: 'info'
              });

              _this4.init();
            }
          }).catch(function (e) {
            return _this4.$message({
              message: e.response.data,
              type: 'error'
            });
          });
          _this4.infoDialog = false;
        });
      }
    },
    checkMove: function checkMove(item) {
      var _this5 = this;

      var message = '确定要移除ta吗？';
      this.$confirm(message).then(function () {
        removeCoworker(_this5.Info.id, item.userId).then(function (res) {
          if (res.status === 200) {
            _this5.Info.coworkers = [];
            res.data.forEach(function (i, index) {
              if (index === 0) {
                _this5.Info.builder = i.username;
                _this5.Info.builderId = i.id;
              }

              _this5.Info.coworkers.push({
                userId: i.id,
                userName: i.username,
                userImg: i.head,
                isBuilder: i.role === 1 ? true : false
              });
            });

            _this5.init(); //好像不需要


            _this5.Dialog = false;

            _this5.$message({
              message: '移除成功',
              type: 'info'
            });
          } else {
            _this5.$message({
              message: '发生其他错误',
              type: 'error'
            });
          }
        }).catch(function (e) {
          if (e.response.status === 401) {
            _this5.$message({
              message: '您没有删除权限或此人不在团队中',
              type: 'error'
            });
          }
        });
        _this5.infoDialog = false;
      });
    }
  }
};